"use client";

import { motion } from "framer-motion";
import { Circle } from "lucide-react";
import { cn } from "@/lib/utils";
import { ElegantShape } from "./elegant-shape";
import { Button } from "@/components/ui/button";
import { useState } from "react";
import QRCode from "react-qr-code";
import Link from "next/link";

interface HeroProps {
  badge?: string;
  title1?: string;
  title2?: string;
}

export function Hero({
  badge = "AI-Powered Healthcare",
  title1 = "Book Your Doctor",
  title2 = "With Ease and Intelligence",
}: HeroProps) {
  const [showQRCode, setShowQRCode] = useState(false);

  const handleWhatsAppBooking = () => {
    setShowQRCode(true);
  };

  const fadeUpVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: (i: number) => ({
      opacity: 1,
      y: 0,
      transition: {
        duration: 1,
        delay: 0.5 + i * 0.2,
        ease: [0.25, 0.4, 0.25, 1],
      },
    }),
  };

  return (
    <div className="relative min-h-screen w-full flex items-center justify-center overflow-hidden bg-[#030303]">
      <div className="absolute inset-0 bg-gradient-to-br from-blue-500/[0.05] via-transparent to-purple-500/[0.05] blur-3xl" />

      <div className="absolute inset-0 overflow-hidden">
        <ElegantShape
          delay={0.3}
          width={600}
          height={140}
          rotate={12}
          gradient="from-blue-500/[0.15]"
          className="left-[-10%] md:left-[-5%] top-[15%] md:top-[20%]"
        />

        <ElegantShape
          delay={0.5}
          width={500}
          height={120}
          rotate={-15}
          gradient="from-purple-500/[0.15]"
          className="right-[-5%] md:right-[0%] top-[70%] md:top-[75%]"
        />

        <ElegantShape
          delay={0.4}
          width={300}
          height={80}
          rotate={-8}
          gradient="from-cyan-500/[0.15]"
          className="left-[5%] md:left-[10%] bottom-[5%] md:bottom-[10%]"
        />

        <ElegantShape
          delay={0.6}
          width={200}
          height={60}
          rotate={20}
          gradient="from-teal-500/[0.15]"
          className="right-[15%] md:right-[20%] top-[10%] md:top-[15%]"
        />

        <ElegantShape
          delay={0.7}
          width={150}
          height={40}
          rotate={-25}
          gradient="from-indigo-500/[0.15]"
          className="left-[20%] md:left-[25%] top-[5%] md:top-[10%]"
        />
      </div>

      <div className="relative z-10 container mx-auto px-4 md:px-6">
        <div className="max-w-3xl mx-auto text-center">
          <motion.div
            custom={0}
            variants={fadeUpVariants}
            initial="hidden"
            animate="visible"
            className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-white/[0.03] border border-white/[0.08] mb-8 md:mb-12"
          >
            <Circle className="h-2 w-2 fill-blue-500/80" />
            <span className="text-sm text-white/60 tracking-wide">{badge}</span>
          </motion.div>

          <motion.div
            custom={1}
            variants={fadeUpVariants}
            initial="hidden"
            animate="visible"
          >
            <h1 className="text-4xl sm:text-6xl md:text-7xl font-bold mb-6 md:mb-8 tracking-tight">
              <span className="bg-clip-text text-transparent bg-gradient-to-b from-white to-white/80">
                {title1}
              </span>
              <br />
              <span
                className={cn(
                  "bg-clip-text text-transparent bg-gradient-to-r from-blue-300 via-white/90 to-purple-300"
                )}
              >
                {title2}
              </span>
            </h1>
          </motion.div>

          <motion.div
            custom={2}
            variants={fadeUpVariants}
            initial="hidden"
            animate="visible"
          >
            <p className="text-base sm:text-lg md:text-xl text-white/40 mb-8 leading-relaxed font-light tracking-wide max-w-xl mx-auto px-4">
              Experience the future of healthcare scheduling with our AI-powered
              appointment booking system.
            </p>
          </motion.div>

          <motion.div
            custom={3}
            variants={fadeUpVariants}
            initial="hidden"
            animate="visible"
            className="flex justify-center gap-4"
          >
            <Button
              size="lg"
              className="bg-green-600 hover:bg-green-700 text-white"
              onClick={handleWhatsAppBooking}
            >
              Book on WhatsApp
            </Button>
            <Link href="http://localhost:3001" target="_blank">
              <Button
                size="lg"
                variant="outline"
                className="bg-white text-blue-600 border-blue-600 hover:bg-blue-100"
              >
                Book on Website
              </Button>
            </Link>
          </motion.div>

          {showQRCode && (
            <motion.div
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              className="fixed inset-0 bg-black/80 flex items-center justify-center z-50"
              onClick={() => setShowQRCode(false)}
            >
              <div
                className="bg-white p-8 rounded-lg text-center"
                onClick={(e) => e.stopPropagation()}
              >
                <h3 className="text-xl font-bold mb-4">
                  Scan to Book on WhatsApp
                </h3>
                <QRCode
                  value={`https://wa.me/14155238886?text=Bonjour%2C%20je%20souhaite%20prendre%20rendez-vous`}
                />
                <p className="mt-4 text-sm text-gray-600">
                  Or click{" "}
                  <a
                    href="https://wa.me/14155238886?text=Bonjour%2C%20je%20souhaite%20prendre%20rendez-vous"
                    className="text-blue-600 hover:underline"
                  >
                    here
                  </a>{" "}
                  to open WhatsApp
                </p>
                <Button
                  className="mt-4 bg-gray-200 hover:bg-gray-300 text-gray-800"
                  onClick={() => setShowQRCode(false)}
                >
                  Close
                </Button>
              </div>
            </motion.div>
          )}
        </div>
      </div>

      <div className="absolute inset-0 bg-gradient-to-b from-[#030303] via-transparent to-[#030303]/80 pointer-events-none" />
    </div>
  );
}
